<template>
  <v-col cols="12" sm="12" md="9" lg="9">
    <v-sheet outlined class="rounded-lg pa-2">
      <v-sheet flat outlined class="overflow-hidden rounded-lg" max-height="450">
        <v-app-bar
          elevate-on-scroll
          scroll-target="#scrolling-techniques-7"
          :color="$vuetify.theme.dark ? 'dark' : 'white'"
        >
          <v-toolbar-title>Data Cuti</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon>
            <v-icon>mdi-magnify</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </v-app-bar>

        <v-divider></v-divider>

        <v-list 
          class="overflow-y-auto"
          max-height="380"
          two-line
          style="height: 450px;"
          id="scrolling-techniques-7"
        >
          <div v-show="loading == true">
            <v-skeleton-loader
              height="50"
              class="mt-n3"
              type="list-item-two-line"
            ></v-skeleton-loader>
          </div>

          <v-list-item-group
            v-model="selected"
            active-class="red--text"
          >
            <template v-for="(item,index) in items">
              <v-list-item :key="index+'x'">
                <template>
                  <v-list-item-content @click="DialogPesan(item)">
                    <v-list-item-title v-text="item.JenisCuti"></v-list-item-title>

                    <!-- <v-list-item-subtitle
                      class="text--primary"
                      v-text="item.headline"
                    ></v-list-item-subtitle> -->

                    <v-list-item-subtitle v-text="item.AlasanCuti"></v-list-item-subtitle>
                  </v-list-item-content>

                  <v-list-item-action>
                    <v-list-item-action-text v-text="item.DibuatTgl"></v-list-item-action-text>
                    <!-- <v-icon v-if="!active" color="grey lighten-1">mdi-email-send-outline</v-icon>
                    <v-icon v-else color="red darken-4">mdi-email-send</v-icon> -->
                  </v-list-item-action>
                </template>
              </v-list-item>

              <v-divider
                v-if="index < items.length - 1"
                :key="index"
              ></v-divider>
            </template>
          </v-list-item-group>
        </v-list>

        <!-- Dialog Detail Pesan -->
        <v-dialog
          v-model="DialogDetailPesan"
          max-width="400"
        >
          <v-card
            outlined
            class="rounded-lg"
          >
            <v-toolbar flat class="rounded-t-lg">
              <v-btn
                icon
                @click="DialogDetailPesan = false"
              >
                <v-icon large>mdi-chevron-left</v-icon></v-btn>
              <v-toolbar-title>Detail Data Cuti</v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-divider></v-divider>
            <v-card-text>
              <div class="mr-n3 ml-n3 mb-n2 mt-3">
                <v-sheet outlined class="rounded-lg">
                  <div v-show="loading == false">
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title class="subtitle-1">Dibuat</v-list-item-title>
                        <v-list-item-subtitle v-text="''+DetailMsg.karyawan.Nama"/>
                      </v-list-item-content>
                    </v-list-item>
                    <v-divider></v-divider>
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title class="subtitle-1">Diajukan Tanggal</v-list-item-title>
                        <v-list-item-subtitle v-text="''+DetailMsg.TanggalPengajuan"/>
                      </v-list-item-content>
                    </v-list-item>
                    <v-divider></v-divider>
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title class="subtitle-1">Dari Tanggal</v-list-item-title>
                        <v-row dense no-gutters>
                          <div><v-list-item-subtitle v-text="''+DetailMsg.TanggalMulai"/></div>
                          <div class="mr-2 ml-2">S/D</div>
                          <div><v-list-item-subtitle v-text="''+DetailMsg.TanggalSelesai"/></div>
                        </v-row>
                      </v-list-item-content>
                    </v-list-item>
                    <v-divider></v-divider>
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title class="subtitle-1">Jam Mulai</v-list-item-title>
                        <v-row dense no-gutters>
                          <div><v-list-item-subtitle v-text="''+DetailMsg.JamMulai"/></div>
                          <div class="mr-2 ml-2">S/D</div>
                          <div><v-list-item-subtitle v-text="''+DetailMsg.JamSelesai"/></div>
                        </v-row>
                      </v-list-item-content>
                    </v-list-item>
                    <v-divider></v-divider>
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title class="subtitle-1">Jenis Cuti</v-list-item-title>
                        <v-list-item-subtitle v-text="''+DetailMsg.JenisCuti"/>
                      </v-list-item-content>
                    </v-list-item>
                    <v-divider></v-divider>
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title class="subtitle-1">Alasan Cuti</v-list-item-title>
                        <v-list-item-subtitle v-text="''+DetailMsg.AlasanCuti"/>
                      </v-list-item-content>
                    </v-list-item>
                  </div>
                </v-sheet>
                <!-- <v-sheet
                  v-for="(DetailMsg,index) in DetailMsg"
                  :key="index" class="d-flex justify-start pa-1"
                > -->
                  <!-- <v-sheet outlined class="pa-3">
                    <div class="subtitle-2">
                      <label v-text="'Dibuat : '+DetailMsg.karyawan.Nama"/>
                    </div>
                  </v-sheet>
                  <v-sheet outlined class="pa-3">
                    <div class="subtitle-2">
                      <label v-text="'Diajukan Tanggal : '+DetailMsg.TanggalPengajuan"/>
                    </div>
                  </v-sheet>
                  <v-sheet outlined class="pa-3">
                    <div class="subtitle-2">
                      <label v-text="'Dari Tanggal : '+DetailMsg.TanggalMulai"/>
                      <label v-text="' S/D '+DetailMsg.TanggalSelesai"/>
                    </div>
                  </v-sheet>
                  <v-sheet outlined class="pa-3">
                    <div class="subtitle-2">
                      <label v-text="'Jam Mulai : '+DetailMsg.JamMulai"/>
                      <label v-text="' S/D '+DetailMsg.JamSelesai"/>
                    </div>
                  </v-sheet>
                  <v-sheet outlined class="pa-3">
                    <div class="subtitle-2">
                      <label v-text="'Jenis Cuti : '+DetailMsg.JenisCuti"/>
                    </div>
                  </v-sheet>
                  <v-sheet outlined class="pa-3">
                    <div class="subtitle-2">
                      <label v-text="'Alasan Cuti : '+DetailMsg.AlasanCuti"/>
                    </div>
                  </v-sheet> -->
                <!-- </v-sheet> -->
              </div>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-sheet>
    </v-sheet>
  </v-col>
</template>

<script>
import api from "@/services/http";
  export default {
    props: ['Menu'],
    data: () => ({
      loading: true,
      DialogDetailPesan: false,
      selected: [0],
      DetailMsg:{
        AlasanCuti:'',
        DibuatOleh:'',
        DibuatTgl:'',
        DiubahOleh:'',
        DiubahTgl:'',
        JamMulai:'',
        JamSelesai:'',
        JenisCuti:'',
        KodeKaryawan:'',
        NoCuti:'',
        StatusCuti:'',
        TanggalMulai:'',
        TanggalPengajuan:'',
        TanggalSelesai:'',
        id:'',
          karyawan:{
            Nama:''
          }
      },
      token:localStorage.getItem('token'),
      items: [],
    }),
    watch:{
      Menu(){
        if(this.Menu == "DataCuti"){this.getData()}
      }
    },
    methods:{
      DialogPesan(item){
        this.DialogDetailPesan = true
        this.DetailMsg = item
        // console.log(this.DetailMsg)
      },
      getData(){
       api.post('/listcutikaryawan?token='+this.token,{
          KodeKaryawan: this.$user.KodeKaryawan
          },{ headers: { 'Content-Type': 'multipart/form-data',"Authorization" : `Bearer ${this.token}` } })
          .then((res)=>{
            if (res) {
              this.loading = false
              // this.items = res.data.filter(data => data.StatusCuti == 'Dibuat')
              this.items = res.data
              this.items.forEach(d => {
                d.DibuatTgl = d.DibuatTgl.substr(0,10)
              });
              console.log(this.items)
            }
          })
      }
    }
  }
</script>

<style scoped>
.v-app-bar {
  will-change: initial !important;
}
</style>