<template>
  <v-dialog
    v-model="FormCutiKaryawan"
    persistent
    scrollable
    transition="dialog-bottom-transition"
    max-width="900px"
  >
    <v-card outlined class="rounded-lg">
      <v-toolbar flat tile class="rounded-lg">
        <v-btn
          icon
          @click="Tutup"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title class="title">
          <span class="text-h5">Cuti Karyawan</span>
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <!-- <v-toolbar-items>
          <v-btn
            text
            @click="SimpanCuti"
          >
            Simpan
          </v-btn>
          <v-btn
            text
            @click="KirimCuti"
          >
            Kirim
          </v-btn>
        </v-toolbar-items> -->
      </v-toolbar>

      <v-divider></v-divider>

      <v-card-text>
        <div class="mr-n3 ml-n3 mb-n2 mt-3">
          <v-row dense>
            <!-- Menu -->
            <v-col cols="12" sm="12" md="3" lg="3">
              <v-sheet outlined class="rounded-lg">
                <v-list nav dense class="rounded-lg">
                  <v-list-item-group
                    v-model="selectedItem"
                    color="primary"
                  >
                    <v-list-item
                      v-for="(item, i) in items"
                      :key="i"
                      @click="MenuForm(item.to)"
                      color="red darken-4"
                    >
                      <v-list-item-icon>
                        <v-icon v-text="item.icon"></v-icon>
                      </v-list-item-icon>
                      <v-list-item-content>
                        <v-list-item-title v-text="item.text"></v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </v-list-item-group>
                </v-list>
              </v-sheet>
            </v-col>

            <!-- Form -->
            <PengajuanCuti v-show="Menu == 'PengajuanCuti'" :Menu="Menu"/>
            <DrafCuti v-show="Menu == 'DrafCuti'" :Menu="Menu"/>
            <DataCuti v-show="Menu == 'DataCuti'" :Menu="Menu"/>
          </v-row>
        </div>
      </v-card-text>
      
    </v-card>
  </v-dialog>
</template>
<script>

import PengajuanCuti from '@/views/FormCuti/PengajuanCuti'
import DrafCuti from '@/views/FormCuti/DrafCuti'
import DataCuti from '@/views/FormCuti/DataCuti'
  export default {
    components: {
      PengajuanCuti,DrafCuti,DataCuti
    },
    props: ['FormCutiKaryawan'],
    data () {
      return {
        Menu:'PengajuanCuti',
        selectedItem: 0,
        items: [
          { text: 'Pengajuan Cuti', icon: 'mdi-message', to: 'PengajuanCuti' },
          { text: 'Data Cuti', icon: 'mdi-message', to: 'DataCuti' },
          { text: 'Draf Cuti', icon: 'mdi-message', to: 'DrafCuti' },
        ],
      }
    },
    watch:{
      //
    },
    methods:{
        Tutup(){
        this.$emit('FormCutiKaryawan',false)
        this.Cuti = Object.assign({}, this.DefaultCuti)
        },
        MenuForm(data){
          this.Menu = data
        },
        SimpanCuti(){
          alert('ok')
        },
        KirimCuti(){
          alert('kirim')
        }
    }
  }
</script>