<template>
  <v-col cols="12" sm="12" md="9" lg="9">
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dismissible
      color="success"
      border="left"
      elevation="4"
      colored-border
      v-model="Alert"
      transition="slide-x-transition"
    >
      <v-icon class="mr-1">check-circle</v-icon>
      {{Pesan}}
    </v-alert>

    <v-sheet outlined class="rounded-lg pa-2">
      <v-sheet outlined class="rounded-lg">
        <v-toolbar
          flat
          class="rounded-t-lg"
          :color="$vuetify.theme.dark ? 'dark' : 'white'"
        >
          <v-toolbar-title>Pengajuan Cuti</v-toolbar-title>
        </v-toolbar>
        <v-divider></v-divider>
        <div class="mt-1 pa-5">
          <v-row>
            <v-col cols="12" sm="6" md="6" lg="6">
              <v-dialog
                v-model="MenuTglPengajuan"
                :return-value.sync="date"
                width="290px"
                >
                <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                    v-model="Cuti.TanggalPengajuan"
                    label="Tanggal Pengajuan"
                    prepend-icon="mdi-calendar"
                    color="dark"
                    readonly
                    dense
                    v-bind="attrs"
                    v-on="on"
                    ></v-text-field>
                </template>
                <v-date-picker
                    v-model="Cuti.TanggalPengajuan"
                    @input="MenuTglPengajuan = false"
                    year-icon="mdi-calendar-blank"
                    color="red darken-4"
                    class="rounded-lg"
                    locale="id"
                ></v-date-picker>
              </v-dialog>
            </v-col>

            <v-col cols="12" sm="6" md="6" lg="6">
              <v-dialog
                v-model="MenuTglMulai"
                :return-value.sync="date"
                width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="Cuti.TanggalMulai"
                    label="Tanggal Mulai"
                    prepend-icon="mdi-calendar"
                    color="dark"
                    readonly
                    dense
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="Cuti.TanggalMulai"
                  @input="MenuTglMulai = false"
                  year-icon="mdi-calendar-blank"
                  color="red darken-4"
                  class="rounded-lg"
                  locale="id"
                ></v-date-picker>
              </v-dialog>
            </v-col>

            <v-col cols="12" sm="6" md="6" lg="6">
              <v-dialog
                v-model="MenuTglSelesai"
                :return-value.sync="date"
                width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="Cuti.TanggalSelesai"
                    label="Tanggal Selesai"
                    prepend-icon="mdi-calendar"
                    color="dark"
                    readonly
                    dense
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="Cuti.TanggalSelesai"
                  @input="MenuTglSelesai = false"
                  year-icon="mdi-calendar-blank"
                  color="red darken-4"
                  class="rounded-lg"
                  locale="id"
                ></v-date-picker>
              </v-dialog>
            </v-col>

            <v-col cols="12" sm="6" md="6" lg="6">
              <v-text-field
                dense
                clearable
                v-model="Cuti.JamMulai"
                label="Jam Mulai"
                color="dark"
                type="time"
                hint="Wajib Diisi." 
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="6" md="6" lg="6">
              <v-text-field
                dense
                clearable
                v-model="Cuti.JamSelesai"
                label="Jam Selesai"
                color="dark"
                type="time"
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="6" md="6" lg="6">
              <v-text-field
                dense
                v-model="Cuti.JenisCuti"
                clearable
                label="Jenis Cuti"
                color="dark"
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="12" md="12" lg="12">
              <v-textarea
                dense
                no-resize
                rows="2"
                v-model="Cuti.AlasanCuti"
                label="Alasan Cuti"
                color="dark"
              ></v-textarea>
            </v-col>
          </v-row>
        </div>
        <v-card-actions class="mt-n4">
          <v-btn outlined depressed class="text-capitalize" color="red darken-4" dark bottom right @click="SimpanCuti">Simpan Di Draf</v-btn>
          <v-btn depressed class="text-capitalize" color="red darken-4" dark bottom right @click="KirimCuti">Kirim</v-btn>
        </v-card-actions>
      </v-sheet>
    </v-sheet>
  </v-col>
</template>
<script>
import api from "@/services/http";
export default {
  data () {
    return {
        date: new Date().toISOString().substr(0, 10),
          notifications: false,
          token:localStorage.getItem('token'),
          MenuTglSelesai: false,
          MenuTglMulai: false,
          IntervalAlert:'',
          MenuTglPengajuan: false,
          Cuti:{
            TanggalPengajuan:'',
            TanggalMulai:'',
            TanggalSelesai:'',
            JamMulai:'',
            JamSelesai:'',
            JenisCuti:'',
            AlasanCuti:'',
          },
          DefaultCuti:{
            TanggalPengajuan:'',
            TanggalMulai:'',
            TanggalSelesai:'',
            JamMulai:'',
            JamSelesai:'',
            JenisCuti:'',
            AlasanCuti:'',
          },
          Alert:false,
          Pesan:''
    }
  },
  methods:{ 
      KirimCuti(){
        api.post('/karyawancuti?token='+this.token,{
          KodeKaryawan: this.$user.KodeKaryawan,
          TanggalPengajuan: this.Cuti.TanggalPengajuan,
          TanggalMulai: this.Cuti.TanggalMulai,
          TanggalSelesai: this.Cuti.TanggalSelesai,
          JamMulai: this.Cuti.JamMulai,
          JamSelesai: this.Cuti.JamSelesai,
          JenisCuti:this.Cuti.JenisCuti,
          AlasanCuti: this.Cuti.AlasanCuti,
          StatusCuti:"Diajukan"
          },{ headers: { 'Content-Type': 'multipart/form-data',"Authorization" : `Bearer ${this.token}` } })
          .then((res)=>{
            if (res) {
            this.NotifCutiKaryawan()
              this.Cuti = Object.assign({}, this.DefaultCuti)
            } else {
              //
              this.Cuti = Object.assign({}, this.DefaultCuti)
            }
          })
      },
      SimpanCuti(){
        api.post('/karyawancuti?token='+this.token,{
          KodeKaryawan: this.$user.KodeKaryawan,
          TanggalPengajuan: this.Cuti.TanggalPengajuan,
          TanggalMulai: this.Cuti.TanggalMulai,
          TanggalSelesai: this.Cuti.TanggalSelesai,
          JamMulai: this.Cuti.JamMulai,
          JamSelesai: this.Cuti.JamSelesai,
          JenisCuti:this.Cuti.JenisCuti,
          AlasanCuti: this.Cuti.AlasanCuti,
          StatusCuti:"Dibuat"
          },{ headers: { 'Content-Type': 'multipart/form-data',"Authorization" : `Bearer ${this.token}` } })
          .then((res)=>{
            if (res) {
              this.ShowAlert('Simpan')
              this.Cuti = Object.assign({}, this.DefaultCuti)
            } else {
              //
              this.Cuti = Object.assign({}, this.DefaultCuti)
            }
          })
      },
      NotifCutiKaryawan(){
        api.post('/notifikasi?token='+this.token,{
          Kategori: "Approval",
          Judul: "Pengajuan Cuti",
          Konten: this.Cuti.AlasanCuti,
          Attachment:null,
          Dari: this.$user.KodeKaryawan,
          Tujuan: "Human Resources",
          },
          { headers: { 'Content-Type': 'multipart/form-data',"Authorization" : `Bearer ${this.token}` } }
          )
          .then((res)=>{
            if (res) {
              // this.Alert = true
              // console.log('ok lanjut')
            } else {
              //
            }
          })
      },
      ShowAlert(data){
        if (data == 'Simpan') {
          this.Pesan = 'Pengajuan Cuti Di Simpan Ke Draf'
          this.Alert = true
          clearInterval(this.IntervalAlert)
          this.AlertInterval()
        }else if(data == 'Kirim'){
          this.Pesan = 'Pengajuan Cuti Di Kirim Ke HR'
          this.Alert = true
          clearInterval(this.IntervalAlert)
          this.AlertInterval()
        }
      },
      AlertInterval(){
        this.IntervalAlert = setInterval(() => {
          this.Alert = false;
          this.Pesan = '';
        }, 3000)
      },
  }
}
</script>